        .then((data) => {
          // 检查登录成功
        })
        .catch((error) => {
          // 检查登录失败
          localStorage.removeItem("token");
          this.$router.push({ name: "Auth" });
        });
    },
  },
  created() {
    this.requestCheckLogin();
  },
};
